.login__container{
    position: relative;
    height: 100%;

    background:url("./login__banner.webp") center no-repeat;
    background-size: cover;
}

.netflix__logo{
    position: fixed;
    left: 0;
    width:150px;
    object-fit: contain;
    padding-left:20px;
}

.login__signin-btn{
    position: fixed;
    right:20px;
    top:20px;
    padding:10px 20px;
    font-size: 1rem;
    color:#fff;
    background-color: #e50914;
    font-weight: 600;
    border:none;
    cursor:pointer;
}

.login__signin-btn:hover{
    background-color: red;
}

.login__bg{}

.login__gradient{
    width:100%;
    z-index: 1;
    height:100vh;
    background:rgba(0,0,0,0.4);
    background-image: linear-gradient(to top,
    rgba(0,0,0,0.8) 0, 
    rgba(0,0,0,0) 60% , 
    rgba(0,0,0,0.8) 100%);
}

.login__body{
    z-index: 1;
    padding:20px;
    position: absolute;
    top:30%;
    text-align: left;
    margin-left: auto;
    margin-right: auto;
    text-align: center;

    left:0;
    right:0;
}

.login__body > h1{
    font-size: 3.125rem;
    margin-bottom: 20px;
    
}

.login__body > h2{
    font-size: 2rem;
    font-weight: 400;
    margin-bottom: 30px;
}

.login__body > h3{
    font-size: 1.3rem;
    font-weight: 400;
}

.login__input{
    margin:20px;
}

.login__input > form > input{
    padding:10px;
    outline-width: 0;
    height:30px;
    width:30%;
    border:none;
    max-width: 600px;
}

.get__started-btn{
    padding:16px 20px;
    font-size: 1rem;
    color:#fff;
    background-color: #e50914;
    border:none;
    font-weight: 600px;
    cursor: pointer;
}