.navbar__container{
    position: fixed;
    top: 0;
    padding: 20px;
    width:100%;
    height:30px;
    z-index: 1;

    transition-timing-function: ease-in;
    transition: all 0.5s;
}

.nav__bg{
    background: #111;
}

.nav__bg__transperent{
    background: transparent;
}

.navbar__contents{
    display:flex;
    justify-content:space-between;
}

.nav_logo{
    position: fixed;
    top:10px;
    left:0;
    width:80px;
    object-fit:contain;
    padding-left: 20px;
    cursor: pointer;
}

.nav_avatar{
    position: fixed;
    right: 20px;
    width:30px;
    cursor:pointer;
}

.links__flex{
    display: flex;
    list-style-type: none;
    gap:2rem;
   margin-left: auto;
   margin-right: auto;
}

.links__flex > li > a{
    color:white;
    text-decoration: none;
}
